<template>
  <div>
    <Item-card v-if="menu_stat"/>
    <Cart :cart="cart" v-if="items.length > 0"/>
    <b-button v-if="cart.length > 0 && menu_stat" @click="menu_stat = false">
      ÁµêÂ∏≥
    </b-button>
    <b-button v-if="!menu_stat" @click="menu_stat = true">
      ÂõûËèúÂñÆ
    </b-button>
    <checkout v-if="!menu_stat" :cart="cart" @checkout="menu_stat = true"/>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import ItemCard from '@/components/ItemCard'
import Cart from '@/components/cart'
import checkout from '@/components/checkout'

export default {
  name: 'ItemOrder',
  components: { ItemCard, Cart, checkout },

  data () {
    return {
      menu_stat: true
    }
  },
  computed:{
    ...mapState(['cart', 'items']),
  },
}
</script>


