<template>
  <div>
    <div v-if="cash_mode">
      <div class="container">
        <div class="name">Á∏ΩÂÉπ</div>
        <div class="value">{{ summary.price }}</div>
        <div class="name">ÁèæÈáë</div>
        <div class="value" @click="cash_mode = true" style="background: pink">{{ cash }}</div>
        <b-button @click="key_type = 'ten'" :variant="key_type === 'ten' ? 'success':'light'">Êï∏Â≠óÈçµÁõ§</b-button>
        <b-button @click="key_type = 'int'" :variant="key_type === 'int' ? 'success':'light'">Êï¥Êï∏</b-button>
      </div>
      <b-card>
        <Num-pad :keyType="key_type" @touchBtn="cash = $event" @confrim="cash_mode = false" @equal="cash = price"></Num-pad>
      </b-card>
    </div>
    <div v-if="!cash_mode">
      <div class="container">
        <div class="name">Á∏ΩÂÉπ</div>
        <div class="value">{{ summary.price }}</div>
        <div class="name">
          ÁèæÈáë
        </div>
        <b-button class="value" @click="cash_mode = true" variant="outline-danger">
          {{ cash }}
        </b-button>
      </div>
      <div class="container" v-if="cash - summary.price > 0">
        <div class="name">ÊâæÈõ∂</div>
        <div class="value" :style="(cash - summary.price < 1000) ? 'color: blue' : 'background: red'">{{ cash - summary.price }}</div>
      </div>
      <div class="container">
        <div>
          <b-button class="value" v-for="(item, index) in btnStats" :key="index" @click="setStat(item)" :variant="stat === item ? 'danger':'light'">{{ item }}</b-button>
        </div>
        <b-button class="value" @click="checkoutCart(summary), $emit('checkout')" :disabled="checkoutDisable" :variant="!checkoutDisable ? 'success':'light'">ÁµêÂ∏≥</b-button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapActions, mapState } from 'vuex'
import NumPad from '@/components/NumPad'

export default {
  name: 'checkout',
  components: { NumPad },
  props: {
    cart: {
      type: Array,
      default: () => []
      },
    btnStats: {
      type: Array,
      default: () => ['Â§ñÂ∏∂', 'ÂÖßÁî®']
    }
  },
  data () {
    return {
      key_type: 'int',
      price: null,
      cash: 0,
      stat: null,
      cash_mode: false,
    }
  },
  computed: {
    ...mapState(['userInformation']),
    summary: function () {
      let result = this.cart
        .reduce((result, item) => {
          result.items.push(item)
          result.price += ( item.price * item.number )
          return result
        },
          {
            items: [],
            price: 0,
            cash: this.cash, // cash here
            stat: this.stat
          }
        )
      this.price = result.price
      return result
    },
    checkoutDisable: function () {
      let statCheck = this.stat || this.btnStats.length === 0
      let cartCheck = this.cart.length > 0
      let cashCheck = this.cash - this.price >= 0
      let changeCheck = this.cash - this.price < 1000
      return !(statCheck && cartCheck && cashCheck && changeCheck)
    }
  },
  methods: {
    ...mapMutations(['CHECKOUT_CART']),
    ...mapActions(['fbPost']),
    setStat: function (stat) {
      this.stat = stat
    },
    checkoutCart: async function (summary) {
      let res = await this.fbPost([`shops/${this.userInformation.shopId}/carts`, summary])
      if(res.id){
        Object.keys(this.$data).forEach(e => this.$data[e] = null)
        this.CHECKOUT_CART()
      }
    }
  },
}
</script>
<style lang="stylus" scoped>
.container
  display grid || inline-grid
  grid-template-columns 1fr 1fr
.name
.value
  border .1em double black
  font-size 2em
  text-shadow 0 1px 1px rgba(0, 0, 0, 0.9)
.name
  color black
</style>


