<template>
  <div>
    <div v-if="!fetched">
      <b-button @click="getShopItem">ÂèñÂæóÂïÜÂìÅÊ∏ÖÂñÆ</b-button>
    </div>
    <div v-else>
      <b-card v-if="!Object.keys(products).length">
        <h1>Ê≤íÊúâÂïÜÂìÅÔºåË´ãËá≥Ë®≠ÂÆöÈ†ÅÈù¢Á¢∫Ë™ç</h1><b-link to="Settings">ÈªûÊàë</b-link>
      </b-card>
      <b-card
      v-else
      v-for="(item, index) in products" :key="index"
      :body-bg-variant="item.is_selected ? 'light':'white'" 
      :text-variant="item.is_selected ? 'primary':'dark'"
      @click="select(item.name)">
        <p class="name">
          {{ item.name }}: {{ item.price }}$<span class="name-tag" v-if="item.specials_price_total > 0">+{{item.specials_price_total}}</span>
        </p>
        <h2>
          <b-button @click="changeNumber('plus', item)" variant="outline-primary">Ôºã</b-button>
            {{ item.number }}
          <b-button @click="changeNumber('minus', item)" variant="outline-primary">Ôºç</b-button>
          <b-button @click="addToCart(item)" :disabled="!Boolean(item.number)" :variant="!Boolean(item.number)?'outline-danger':'danger'">üõí</b-button>
        </h2>
        <Custom-list :item="item">i am here to help child re-render when props change but never show up</Custom-list>
      </b-card>
    </div>
  </div>
</template>

<script>
import {mapState, mapActions, mapMutations} from 'vuex'
import CustomList from '@/components/CustomList'
export default {
  name: 'ItemCard',
  components: { CustomList },
  data () {
    return {
      selected: null,
      products: null,
    }
  },
  mounted () {
    this.init()
  },
  computed: {
    ...mapState([
      'userInformation','items'
    ]),
    fetched: function() {
      if(this.items){
        this.feedProducts()
        return true
      } else {
        return false
      }
    },
  },
  methods: {
    ...mapActions(['fbGet']),
    ...mapMutations(['CHECKOUT_ITEM']),
    feedProducts: function () {
      let obj = {}
        this.items.forEach(e => {
          if(e.specials !== undefined){
            e.specials.forEach(sp => sp.price = parseInt(sp.price, 10))
          }
          obj[e.name] = {
            id: e.id,
            name: e.name,
            price: parseInt(e.price, 10),
            price_total: 0,
            specials: e.specials,
            specials_price_total: 0,
            specials_selected: [],
            number: 0,
            is_selected: false
          }
        })
      this.products = obj
      //
      let keys = Object.keys(this.products)
      for(let i = 0; i < keys.length; i+=1){
        if(this.products[keys[i]].specials !== undefined){
          this.products[keys[i]].specials.forEach(e => {
            Object.assign(e, {is_selected: false})
          })
        }
      }
    },
    specialAdd: function (item) {
      let reducer = item.specials.reduce(function (result, e) {
        if(e.is_selected) {
          let obj = {
            name: e.name,
            price: e.price
          }
          result.arr.push(obj)
          result.num += e.price
        }
        return result
      }, {
        arr: [],
        num: 0
      })

      item.specials_selected = reducer.arr
      item.specials_price_total = reducer.num

      this.calculatePriceTotal(item)
    },
    init(){
      this.getShopItem();
    },
    getShopItem: function() {
      this.fbGet([`shops/${this.userInformation.shopId}/items`, 'items'])
    },
    select: function(name) {
      Object.keys(this.products).forEach(e => this.products[e].is_selected = false)
      this.products[name].is_selected = true
      this.selected = name
    },
    changeNumber: function(type, item) {
      switch(type){
        case 'plus':
          item.number += 1
          break
        case 'minus':
          if(item.number > 0){
            item.number -= 1
          }
          break
      }
      this.calculatePriceTotal(item)
    },
    calculatePriceTotal: function(item) {
      item.price_total = item.specials_price_total + item.price
    },
    addToCart: function(item) {
      let spid = ''
      if(item.specials_selected.length > 0){
        item.specials_selected.forEach(e => {
          spid += e.name
        })
      }

      let obj = {
        name: item.name,
        specials_id: spid,
        specials: item.specials_selected,
        number: item.number,
        price: item.price_total,
      }
      this.CHECKOUT_ITEM(obj)
      this.feedProducts()
    }
  }
}
</script>
<style lang="stylus" scoped>
p
  white-space nowrap
.name
  font-size 2em
.name-tag
  font-size .5em
  color red
  border 1px solid black
</style>


